import { Form, Button, Container, Row, Col } from 'react-bootstrap';

import '../styles/form.scss';
import { useSelector } from 'react-redux';
import { useState } from 'react'
import swal from 'sweetalert';
import { useDispatch } from 'react-redux'
import { addCliente } from '../features/datosSlice'


export default function Formulario(params) {
   

    const dispatch = useDispatch();
    const [values, setValues] = useState({});
    const [error, setErrors] = useState({})
    const mistate = useSelector(state => state.datos)


    function onHandleChange(e) {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors(validate({ ...values, [e.target.name]: e.target.value }))
    }

    function validate(input) {
        let errors = {};
        if (!/^[a-zA-Z ']+$/.test(input.nombre)) {
            errors.name = 'nombre no valido'
        }
        if (!/^[a-zA-Z ']+$/.test(input.apellido)) {
            errors.apellido = 'apellido no valido'
        }

        if (!input.fechaNacimiento) {
            errors.fechaNacimiento = 'no hay lugar de nacimiento'
        }
        if (!input.lugarNacimiento) {
            errors.lugarNacimiento = 'no hay lugar de nacimiento'
        }
        if (!input.tipoDocumento) {
            errors.tipoDocumento = 'no es valido el tipo de documento'
        }
        if (!input.expDocumento) {
            errors.expDocumento = 'no es valido el tipo de documento'
        }
        if (!input.fechaExpDocumento) {
            errors.fechaExpDocumento = 'no es valido fecha de documento'
        }

        if (!/^\s*[0-9]{1,10}\s*$/.test(input.numDocumento)) {
            errors.numDocumento = "ducumento invalido"
        }
        if (!/^\s*[0-9]{1,10}\s*$/.test(input.celular)) {
            errors.celular = "celular invalido"
        }
        if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(input.correo)) {
            errors.correo = 'correo invalido'
        }

        return errors
    }
    function onHandleSubmit(e) {
        e.preventDefault();

        if (Object.keys(values).length === 0 || Object.keys(error).length !== 0) {
            swal({
                title: "ERROR!",
                text: "verifica los campos.",
                icon: "warning",
                dangerMode: true,
            });
        }
        else{
            dispatch(addCliente(values))
            console.log(mistate)
           
        }
    }

    return (
        <Container>
            <Row>
                <Col>
                    <Form className='login-box' onSubmit={onHandleSubmit}>
                        <Row>
                            <h2>Informacion Personal</h2>

                        </Row>
                        <Row className="mb-3 ">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.name ? 'dang':''}>Nombres {error.name ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='nombre' onChange={onHandleChange} />
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.apellido ? 'dang':''}>Apellidos{error.apellido ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='apellido' onChange={onHandleChange} />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.lugarNacimiento ? 'dang':''}>Lugar de nacimiento {error.lugarNacimiento ? '⚠':''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='lugarNacimiento' onChange={onHandleChange}>
                                    <option>Seleccione</option>
                                    <option>hola</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.fechaNacimiento ? 'dang':''}>Fecha de Nacimiento {error.fechaNacimiento ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="date" name='fechaNacimiento' onChange={onHandleChange} />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label  className={error.tipoDocumento ? 'dang':''}>Tipo de Documento {error.fechaNacimiento ? '⚠':''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='tipoDocumento' onChange={onHandleChange}>
                                    <option >Seleccione</option>
                                    <option >Cedula de Ciudadania</option>
                                    <option >Pasaporte</option>
                                    <option >Cedula de Extranjeria</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.numDocumento ? 'dang':''}>Numero de Documento {error.numDocumento ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='numDocumento' onChange={onHandleChange} />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.expDocumento ? 'dang':''}>Lugar de Expedicion {error.expDocumento ? '⚠':''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='expDocumento' onChange={onHandleChange}>
                                    <option>Seleccione</option>
                                    <option>Bogota</option>
                                </Form.Select>
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.fechaExpDocumento ? 'dang':''}>Fecha de Expedicion {error.fechaExpDocumento ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="date" name='fechaExpDocumento' onChange={onHandleChange} />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.celular ? 'dang':''}>Numero de celular {error.celular ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='celular' onChange={onHandleChange} />

                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.correo ? 'dang':''}>Correo Electonico {error.correo ? '⚠':''}</Form.Label>
                                <Form.Control className='shadow-none' type="email" name='correo' onChange={onHandleChange} />
                            </Form.Group>
                        </Row>
                        <Row>
                            <Button variant="new" type="submit">
                                Siguiente
                            </Button>
                        </Row>
                    </Form>
                </Col>

            </Row>
        </Container>
    );

}