import { createSlice } from "@reduxjs/toolkit";

const initialState = {}

const reducers = {
    addCliente: (state, action) => {
        return {...state , ...action.payload}
      
    }
}

export const datosSlice = createSlice({
    name: 'datos',
    initialState,
    reducers
})


export const { addCliente } = datosSlice.actions
export default datosSlice.reducer